.App {
  background: white;
}

.hidden {
  display: none;
}

.blue {
  color: #4285f4;
}

.red {
  color: #ea4335;
}

.yellow {
  color: #fbbc05;
}

.green {
  color: #34a853;
}

.title {
  text-align: center;
  text-decoration: none;
  color: black;
  font-size: 24px;
  font-weight: 700;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.body {
  position: absolute;

  display: flex;
  justify-content: center;
  flex-direction: column;

  width: calc(100% - 280px);
  padding: 45px 40px 40px 240px;
}

.body-title {
  font-family: Overpass;
  font-size: 24px;
  width: 100%;
  padding-bottom: 18px;
}

.image-container {
  display: flex;
  justify-content: center;
}

.image {
  width: 100%;
}
